import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import {Button, CardActionArea, CardActions } from '@mui/material';
import AudioImage from "../Audio.png";
import ReactPlayer from 'react-player/lazy';
import ReactAudioPlayer from 'react-audio-player';

const CardData=({mediaName,src,description})=>{
if(mediaName=="Image"){
  return (
    <Card>
      <CardActionArea >
        <img src={src} style={{padding:"5px",margin:"5px",width:"90%"}}/>
        <CardContent>
          <Typography variant="body2" color="text.secondary" style={{textAlign:"justify"}}>
         {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
      <Button variant="outlined" style={{borderRadius:"24px",color:"#E34607",borderColor:"#E34607"}} >More Images</Button>
      </CardActions>
    </Card>
  )}

if(mediaName=="Vedio"){
  return (
    <Card >
      <CardActionArea>
          
      <ReactPlayer style={{padding:"15px"}}
                         
                            url= {src}
                            playing={false}
                            controls={true}
                     
                            config={{ file: { 
                                attributes: {
                                  controlsList: 'nodownload'  //<- this is the important bit
                                }
                            }}}
                           

                         
                            
                        />
                       
    
                    
        <CardContent>
          <Typography variant="body2" color="text.secondary" style={{textAlign:"justify"}}>
          {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
      <Button variant="outlined"  style={{borderRadius:"24px",color:"#E34607",borderColor:"#E34607"}}>MORE Videos</Button>
      </CardActions>
    </Card>
  )}
if(mediaName=="Audio"){
  return (
    <Card>
      <CardActionArea>
  <img src={AudioImage} style={{padding:"5px",margin:"5px",width:"90%"}}></img>


   
      <ReactAudioPlayer
  src={src}
  controls
  controlsList="nodownload"
/>  
                                           
        <CardContent>
          <Typography variant="body2" color="text.secondary" style={{textAlign:"justify"}}>
        {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
      <Button variant="outlined"  style={{borderRadius:"24px",color:"#E34607",borderColor:"#E34607"}}>MORE AUDIOS</Button>
      </CardActions>
    </Card>
  )}
  }

  export default CardData;
